import type { RenderNodeProps } from '@ali/4ever-cangjie';
import React from 'react';

export function Container(props: React.PropsWithChildren<{
  strike?: boolean;
  attributes?: RenderNodeProps['attributes'];
}>) {
  const { strike, children, attributes = {} } = props;
  const style: React.CSSProperties = strike ? {
    textDecoration: 'strike-through',
  } : {};

  return (
    <div {...attributes} style={style}>{children}</div>
  );
}
